/*Screen*/
/*////Container/////*/
/*///Colors////*/
/*////Font size///*/
@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/OpenSans/OpenSans-Regular.ttf') format('truetype');
  font-weight: 400;
}
@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/OpenSans/OpenSans-SemiBold.ttf') format('truetype');
  font-weight: 600;
}
@font-face {
  font-family: 'OpenSans';
  src: url('../fonts/OpenSans/OpenSans-Bold.ttf') format('truetype');
  font-weight: 700;
}
@font-face {
  font-family: 'Montserrat';
  src: url('../fonts/Montserrat/Montserrat-Regular.ttf') format('truetype');
  font-weight: 400;
}
@font-face {
  font-family: 'Montserrat';
  src: url('../fonts/Montserrat/Montserrat-SemiBold.ttf') format('truetype');
  font-weight: 600;
}
@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.lines, .menu-toggle {
    display: none;
}

@media screen and (max-width: 599px) {

    .mobile-menu {
        position: relative;
        z-index: 1;
    }

    .mobile-menu .menu-toggle {
        display: flex;
        width: 30px;
        height: 21px;
        position: absolute;
        cursor: pointer;
        opacity: 0;
        z-index: 2;
    }

    .mobile-menu .lines {
        display: flex;
        width: 29px;
        height: 2px;
        margin-bottom: 5px;
        position: relative;
        background: #3E3F58;
        border-radius: 3px;
        z-index: 1;
        transform-origin: 5px 0;
        transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0),
        opacity 0.55s ease;
    }

    .mobile-menu .lines:first-child {
        transform-origin: 0 0;
    }

    .mobile-menu .lines:nth-last-child(2) {
        transform-origin: 0 100%;
    }

    .mobile-menu .menu-toggle:checked ~ .lines {
        opacity: 1;
        transform: rotate(45deg) translate(-3px, -1px);
        background: #36383F;
    }

    .mobile-menu .menu-toggle:checked ~ .lines:nth-last-child(3) {
        opacity: 0;
        transform: rotate(0deg) scale(0.2, 0.2);
    }

    .mobile-menu .menu-toggle:checked ~ .lines:nth-last-child(2) {
        transform: rotate(-45deg) translate(0, -1px);
    }

    .mobile-menu__content {
        position: absolute;
        left: -265px;
        top: 30px;
        width: 300px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.12);
        background-color: #F5F6FA;
        transform: translateX(150%);
        border-radius: 8px;
        transition: .3s;
    }


    .mobile-menu .menu-toggle:checked ~ .mobile-menu__content {
        transform: none;
        transition: .3s;
    }
}

body {
  position: relative;
}
* {
  box-sizing: border-box;
}
.overflow {
  overflow: hidden;
  position: relative;
}
/*Body container*/
.background {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #ECF1F8;
  opacity: 0.4;
  z-index: -1;
}
.container {
  max-width: 1200px;
  width: 100%;
}
.copyright {
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  text-align: center;
  font-family: 'OpenSans', sans-serif;
  font-size: 12px;
  color: #3E4056;
}
/*Header*/
.header {
  min-width: 320px;
  width: 100%;
  height: 112px;
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  font-family: 'OpenSans', sans-serif;
  font-size: 16px;
}
.header__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 19px 10px 23px 16px;
}
@media screen and (max-width: 599px) {
  .header__container {
    padding: 20px 15px 23px 15px;
  }
}
.header__content {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 599px) {
  .header__content:last-child {
    justify-content: center;
  }
}
.header__logo {
  cursor: pointer;
}
.header_shadow {
  box-shadow: 0 12px 12px rgba(0, 0, 0, 0.06);
}
.profile {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 599px) {
  .profile {
    flex-direction: column;
    padding: 10px;
  }
}
.profile__user {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 17px;
}
@media screen and (max-width: 599px) {
  .profile__user {
    margin: 0;
    width: 100%;
    padding: 10px;
    background-color: #ffffff;
    border-radius: 5px 5px 0 0;
    border-bottom: 1px solid #ECF1F8;
  }
}
.profile__name {
  font-weight: 400;
  margin: 0 21px 0 0;
  color: #3E3F58;
  letter-spacing: -0.5px;
}
@media screen and (max-width: 599px) {
  .profile__name {
    flex-grow: 1;
  }
}
.profile__picture {
  margin-right: 12px;
}
.profile__link {
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile__link img {
  max-width: 100%;
}
.icons {
  display: flex;
}
@media screen and (max-width: 599px) {
  .icons {
    flex-direction: column;
    width: 100%;
    padding: 0 10px 10px 10px;
    background-color: #ffffff;
    border-radius: 0 0 5px 5px ;
  }
}
.icons__link {
  text-decoration: none;
  text-align: right;
  position: relative;
}
@media screen and (max-width: 599px) {
  .icons__link:before {
    content: "Notifications";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    color: #3E4056;
  }
  .icons__link:nth-child(2):before {
    content: "Gifts";
  }
  .icons__link:last-child:before {
    content: "Medium";
  }
}
.menu {
  display: flex;
  font-size: 0.87em;
  text-transform: uppercase;
  font-weight: 600;
}
.menu__link {
  color: #3E4056;
  text-decoration: none;
  opacity: 0.6;
}
.menu__link:not(:last-child) {
  margin-right: 37px;
}
.menu__link_active {
  position: relative;
  opacity: 1;
}
.menu__link_active:after {
  content: "";
  position: absolute;
  top: 35px;
  left: 0;
  width: 34px;
  height: 4px;
  background: linear-gradient(135deg, #FF974F 0%, #E32C75 100%);
}
.menu__link:not(.menu__link_active):hover {
  transition: 0.3s;
  background: linear-gradient(135deg, #FF974F 0%, #E32C75 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
/*Main*/
.main {
  min-width: 320px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 72px 0 0 15px;
}
@media screen and (max-width: 1199px) {
  .main__container {
    padding: 72px 0 0;
    max-width: 800px;
  }
}
@media screen and (max-width: 812px) {
  .main__container {
    flex-direction: column;
  }
}
.action-button {
  position: absolute;
  top: 7px;
  right: 5px;
  width: 64px;
  height: 64px;
  background: linear-gradient(135deg, #FF974F 0%, #E32C75 100%);
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
@media screen and (max-width: 599px) {
  .action-button {
    position: fixed;
    top: inherit;
    bottom: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    z-index: 1;
  }
}
.action-button:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 2px;
  background-color: #ffffff;
}
.action-button:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: scale(1) translate(-50%, -50%);
  width: 2px;
  height: 20px;
  background-color: #ffffff;
}
.action-button:hover {
  transition: 0.3s;
  transform: scale(1.25);
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.12);
}
/*User*/
.user {
  display: flex;
  font-family: 'OpenSans', sans-serif;
  font-weight: 400;
  max-width: 350px;
  font-size: 16px;
  color: #3E3F58;
}
@media screen and (max-width: 1199px) {
  .user {
    flex-basis: 50%;
    max-width: 100%;
    justify-content: center;
  }
}
@media screen and (max-width: 812px) {
  .user {
    margin-bottom: 50px;
  }
}
@media screen and (max-width: 599px) {
  .user {
    font-size: 12px;
  }
}
.user__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-basis: 350px;
}
@media screen and (max-width: 812px) {
  .user__wrapper {
    align-items: center;
    text-align: center;
  }
}
@media screen and (max-width: 599px) {
  .user__wrapper {
    flex-basis: 300px;
  }
}
.user__details {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 32px;
}
@media screen and (max-width: 599px) {
  .user__details {
    align-items: center;
  }
}
.user__avatar {
  margin-right: 25px;
}
.user__info {
  display: flex;
  flex-direction: column;
}
.user__name {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 1.5em;
  margin: 0 0 8px 0;
}
.user__nickname {
  margin: 0 0 16px 0;
  letter-spacing: -0.5px;
}
.user__edit {
  display: flex;
}
@media screen and (max-width: 812px) {
  .user__edit {
    justify-content: center;
  }
}
.user__edit-icon {
  margin-right: 7px;
}
@media screen and (max-width: 812px) {
  .user__edit-icon {
    position: absolute;
    margin-right: 0;
    left: 50%;
    transform: translateX(50%);
  }
}
.user__edit-text {
  color: #F05D63;
}
.user__connections {
  padding-left: 4px;
  margin-bottom: 26px;
}
@media screen and (max-width: 812px) {
  .user__connections {
    padding: 0;
  }
}
.user__description {
  margin: 0;
  line-height: 1.5;
}
.user__popup {
  right: 15px;
}
@media screen and (max-width: 812px) {
  .user__popup {
    right: 0;
  }
}
.connections {
  display: flex;
}
.connections__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 48px;
}
.connections__content:last-child {
  margin-right: 0;
}
.connections__number {
  font-size: 1.13em;
  margin-bottom: 5px;
}
.connections__type {
  margin: 0;
}
.popup {
  position: absolute;
}
.popup__dots {
  cursor: pointer;
}
.popup__content {
  position: absolute;
  z-index: 2;
  top: 42px;
  left: 2px;
  display: flex;
  flex-direction: column;
  padding: 24px 0;
  font-weight: 600;
  visibility: hidden;
}
@media screen and (max-width: 689px) {
  .popup__content {
    top: inherit;
    left: inherit;
    bottom: 28px;
    right: 8px;
  }
}
.popup__content.show {
  visibility: visible;
  animation: appear 0.5s;
}
.popup__content:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  border-radius: 8px;
  background-color: #ffffff;
  opacity: 0.94;
  backdrop-filter: blur(21.7463px);
}
.popup__content:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.0001);
  opacity: 0.4;
  box-shadow: 0 24px 24px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  z-index: -2;
}
.popup__row {
  display: flex;
  padding: 8px 23px;
}
.popup__row:hover {
  background-color: #ECF1F8;
  backdrop-filter: blur(54.3656px);
  cursor: pointer;
}
.popup__icon {
  margin-right: 15px;
}
.popup__text {
  white-space: nowrap;
  margin: 0;
}
/*Cards*/
.cards {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding: 0 3px 0 35px;
  margin-bottom: 50px;
}
@media screen and (max-width: 1199px) {
  .cards {
    flex-basis: 50%;
    flex-grow: 0;
    padding: 0;
  }
}
.cards__area {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.cards__switcher {
  margin-bottom: 50px;
}
.card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-basis: 50%;
  font-family: 'OpenSans', sans-serif;
  font-size: 16px;
  color: #3E4056;
}
@media screen and (max-width: 1199px) {
  .card {
    flex-basis: 100%;
  }
}
@media screen and (max-width: 599px) {
  .card {
    font-size: 12px;
  }
}
.card__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  max-width: 374px;
  height: 280px;
  padding: 24px;
  margin: 12px;
  background-color: #ffffff;
  border-radius: 8px;
  cursor: pointer;
}
@media screen and (max-width: 599px) {
  .card__wrapper {
    max-width: 280px;
    height: 200px;
    padding: 14px;
  }
}
.card__wrapper:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 14px;
  z-index: -1;
  opacity: 0.5;
  box-shadow: 0 24px 24px rgba(0, 0, 0, 0.12);
  transition: opacity 0.3s;
}
.card__wrapper:hover.card__wrapper:after {
  opacity: 1;
}
.card__header {
  display: flex;
  flex-grow: 1;
  align-items: center;
  margin-bottom: 8px;
}
.card__link-icon {
  margin-right: 4px;
}
.card__header-text {
  margin: 0 4px 0 0;
  font-weight: 600;
}
.card__tag {
  display: block;
  max-width: 247px;
  padding: 3px 5px 5px 4px;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: #ECF1F8;
  border-radius: 6px;
  font-weight: 600;
}
@media screen and (max-width: 599px) {
  .card__tag {
    max-width: 190px;
  }
}
.card__title {
  font-family: 'Montserrat', sans-serif;
  line-height: 1.24;
  font-size: 2.25em;
  font-weight: 400;
  margin: 0 0 44px 0;
}
@media screen and (max-width: 599px) {
  .card__title {
    margin: 0 0 24px 0;
  }
}
.card__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card__profile {
  display: flex;
  align-items: center;
}
.card__avatar {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #B2B3BB;
  margin-right: 8px;
}
.card__author {
  margin: 0;
  font-weight: 600;
  font-size: 0.88em;
}
.card__link {
  align-self: flex-start;
  font-size: 1.125em;
  text-decoration: none;
  color: #F05D63;
}
@media screen and (max-width: 599px) {
  .card__link {
    align-self: center;
  }
}
.card__stats {
  display: flex;
}
.card__views,
.card__likes {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 0.88em;
}
.card__views:not(.card__likes),
.card__likes:not(.card__likes) {
  margin-right: 16px;
}
.card__view-icon {
  margin-right: 8px;
}
.card__like {
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: none;
  margin-right: 8px;
  width: 16px;
  height: 16px;
  background: url(/build/img/card/icon_like.svg) no-repeat center;
}
.card__counter {
  margin: 0;
}
.switcher {
  display: flex;
  align-items: center;
  padding-left: 24px;
  font-family: 'OpenSans', sans-serif;
  font-size: 16px;
}
@media screen and (max-width: 1199px) {
  .switcher {
    padding: 0;
    justify-content: center;
  }
}
.switcher__text {
  margin: 0;
  font-weight: 600;
}
.switcher__text:not(:last-child) {
  margin-right: 30px;
}
.switcher__label {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
  margin-right: 30px;
}
.switcher__input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switcher__input:checked + .switcher__slider {
  background-color: #F05D63;
}
.switcher__input:focus + .switcher__slider {
  box-shadow: 0 0 1px #F05D63;
}
.switcher__input:checked + .switcher__slider:before {
  transform: translateX(16px);
}
.switcher__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #B2B3BB;
  transition: 0.3s;
}
.switcher__slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: 0.3s;
}
.switcher__round {
  border-radius: 36px;
}
.switcher__round:before {
  border-radius: 50%;
}
